* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Ubuntu", sans-serif;
}

*::-webkit-scrollbar {
  width: 9px;
}

*::-webkit-scrollbar-thumb {
  background-color: #778;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #aab;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #889;
}

*::-webkit-scrollbar-track {
  background-color: #f8f9fa;
}

#root {
  background-color: #f8f9fa;
}

a {
  text-decoration: none;
}

.about {
  width: 100%;
  padding-top: 9rem;
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 10%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

/* FOUNDING EDITORS */
.about-sub-section {
  display: flex;
  flex-grow: 3;
  flex-wrap: wrap;
  place-items: flex-start;
  justify-content: stretch;
}

@media only screen and (max-width: 768px) {
  .about {
    padding-left: 13%;
    padding-right: 13%;
    padding-top: 11rem;
  }
}

@media only screen and (max-width: 426px) {
  .about {
    padding-left: 7%;
    padding-right: 7%;
  }
}

.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  color: #fff;
  cursor: default;
  padding: 0.7rem 0;
  font-size: 15px;
  font-size: 0.9rem;
  text-align: center;
  position: relative;
  background-color: #485fc7;
}

.footer .footerLogoLink {
  color: #fff;
  font-size: 0.9rem;
  font-weight: bold;
  text-decoration: none;
}

.footer .footerLogoLink:hover {
  cursor: pointer;
  text-decoration: underline;
}

/* POSITION OF TOP AND BOTTOM(CATEGORY) NAVS */
.header-section .navbar,
.header-section nav.breadcrumb {
  width: 100%;
  z-index: 1;
  position: fixed;
}

.header-section .navbar div#navbar-basic #navbar-end-1 {
  display: none;
}

/* FOR TOP NAVBAR SECTION */
.header-section .navbar {
  padding: 0 2%;
  padding-right: 3%;
  border-bottom: 1px solid #eee;
}

/* LOGO SECTION */
.header-section .navbar span#logo-section div {
  border-radius: 3px;
  padding: 0.3rem 0.6rem;
}

.header-section .navbar div span#logo-section img {
  min-height: 40px;
}

.header-section .navbar span#logo-section span {
  line-height: 1;
  letter-spacing: 1px;
}

.header-section .navbar-item .buttons button.login-button,
button.about-button {
  height: 2.3rem;
  font-size: 16px;
  transition-duration: 0.3s;
  box-shadow: 1px 2px 2px #1113;
}

.header-section .navbar-item .buttons button.login-button:hover,
button.about-button:hover {
  box-shadow: 3px 4px 4px #1113;
}

.header-section .navbar-item .buttons button *,
button * {
  cursor: pointer !important;
}

.header-section .navbar .navbar-menu form.search-form {
  flex: 1 1;
  display: flex;
}

.header-section .navbar .navbar-menu form.search-form .control {
  flex: 1 1;
}

.header-section .navbar .navbar-menu form.search-form button {
  width: 30px;
  height: 30px;
  margin-left: -2.45rem;
  box-shadow: 0 4px 8px #485fc777;
}

/* LOWER NAVBAR SECTION(NEWS CATEGORIES) */
.header-section nav.breadcrumb {
  flex: 1 1;
  top: 4.2rem;
  width: 100%;
  padding: 0.3rem 1rem;
  box-shadow: 0 3px 4px #2222;
}

.header-section nav.breadcrumb ul {
  flex: 1 1;
  width: 100%;
}

.header-section nav.breadcrumb ul li a {
  font-size: 11px;
}

@media only screen and (max-width: 1024px) {
  .header-section .navbar div.navbar-brand {
    flex: none;
  }

  .header-section .navbar div#navbar-basic {
    flex: 1 1;
    box-shadow: none;
    display: flex !important;
    background-color: transparent;
  }

  .header-section .navbar div#navbar-basic #navbar-end-1 {
    display: flex;
  }

  .header-section .navbar div#navbar-basic #navbar-end-2 {
    display: none;
  }
}

@media only screen and (max-width: 1023px) {
  /* FOR TOP NAVBAR SECTION */
  .header-section .navbar div.navbar-brand {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 768px) {
  /* FOR TOP NAVBAR SECTION ON SMALLER SCREENS */
  .header-section .navbar {
    padding: 0 3% 0 2%;
  }

  .header-section .navbar div#navbar-basic {
    justify-content: flex-end;
  }

  .header-section .navbar div#navbar-basic form {
    display: none;
  }
}

* {
  cursor: default;
}

/* PROFILE CARD */
.profile-card {
  border-radius: 8px;
  box-shadow: 2px 4px 12px #ccc;
}

@media only screen and (max-width: 426px) {
  .profile-card {
    margin-right: 0 !important;
  }
}

.blog-container {
  display: flex;
  padding-top: 9rem;
  min-height: 100vh;
  place-items: center;
  margin-bottom: 90px;
  flex-direction: column-reverse;
}

.blog-container .loading {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog-container .utilities {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.blog-container .utilities .account-alert {
  font-size: 1.2rem;
  font-weight: 500;
  margin-left: 2rem;
  text-align: center;
}

.blog-container .utilities .utility-item {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  cursor: pointer;
}

.blog-container .blog {
  width: 55%;
  display: flex;
  align-self: center;
  justify-content: center;
}

.blog-container .blog .card {
  width: 100%;
  border: none;
  box-shadow: none;
  background-color: transparent;
}

.blog-container .blog .card .author-section {
  display: flex;
  place-items: center;
  justify-content: flex-start;
}

.blog-container .blog .card .blog-image {
  max-height: 28rem;
  border-radius: 2px;
}

.blog-container .blog .card .card-body {
  padding: 0.5rem;
  margin-top: 2rem;
}

.blog-container .blog .card h1 {
  font-weight: 700;
  font-size: 2.2rem;
  margin-top: 2.5rem;
  letter-spacing: 0.08rem;
}

.blog-container .blog .card .postHeader {
  display: flex;
  margin-bottom: 2rem;
  place-items: center;
  justify-content: space-between;
}

.blog-container .blog .card .postHeader .share {
  border-radius: 50%;
  padding: 0.4rem 0.6rem;
  transition-duration: 0.5s;
}

.blog-container .blog .card .postHeader .share:hover {
  cursor: pointer;
  background-color: #aaa5;
}

.blog-container .blog .postHeader .dateCategory {
  display: flex;
  place-items: center;
  justify-content: center;
}

.blog-container .blog .postHeader .dateCategory #divider {
  width: 1px;
  height: 20px;
  margin: 0 0.5rem;
  background-color: #3336;
}

.comment-box form button {
  width: 30%;
  color: #fff;
  display: flex;
  cursor: pointer;
  border-radius: 7px;
  align-items: center;
  margin-bottom: 2rem;
  padding: 0.5rem 1rem;
  justify-content: center;
  background-color: #368dd6;
}

.comment-box form button:hover {
  background-color: #257cc5;
}

.comment-container .comment {
  padding: 1rem 1rem;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.19);
}

.comment-container .comment .comment-author {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}

.comment-container .comment .comment-date {
  font-size: 0.8rem;
  font-weight: 400;
  color: #181717;
  margin-bottom: 0.5rem;
}

.comment-container .comment .comment-content {
  font-size: 1.2rem;
  font-weight: 500;
  color: #181717;
}

.copy-link .copy-link-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-link .copy-link-container .url {
  font-size: 1rem;
  font-weight: 500;
  color: #d64531;
  word-break: break-all;
}

.copy-link .copy-link-container .copy {
  cursor: pointer;
  transition: 0.3s;
}

.copy-link .copy-link-container .copy:hover {
  color: #f33838;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.copy-link .copied-text {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 500;
}

.copy-link .copied-text span {
  border-radius: 7px;
  padding: 0.5rem 1rem;
}

@media only screen and (max-width: 1024px) {
  .blog-container .blog {
    width: 70%;
  }
}

@media only screen and (max-width: 426px) {
  .blog-container .blog {
    width: 90%;
  }
}

